.box {
  height: 270px;
  width: auto;
  padding: 0;
  margin: 0;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
  background-color: transparent;
}

.box-container {
  position: relative;
  height: 270px;
  width: 270px;
  margin: 10px;
}

.box-container:hover .overlay {
  opacity: 1;
  z-index: 1;
}

.box-container .overlay:after {
  opacity: 0;
  -webkit-transition: opacity 0.2s ease;
  -o-transition: opacity 0.2s ease;
  transition: opacity 0.2s ease;
  webkit-transition: opacity 0.2s ease;
}

.box-container:hover .overlay:after {
  opacity: 1;
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.85);
  z-index: -1;
}

.text {
  color: white;
  font-size: 20px;
